import React from 'react';
import {
  BarChart, Bar, LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer
} from 'recharts';

const ChartsDashboard = ({ videos, channelData }) => {
  const videoStats = videos.map(video => ({
    title: video.snippet.title.slice(0, 30) + '...',
    views: parseInt(video.statistics.viewCount),
    likes: parseInt(video.statistics.likeCount || 0),
    comments: parseInt(video.statistics.commentCount || 0),
    likeViewRatio: (
      (parseInt(video.statistics.likeCount || 0) /
        parseInt(video.statistics.viewCount || 1)) * 100
    ).toFixed(2)
  }));

  const cardStyle = {
    backgroundColor: 'white',
    padding: '20px',
    borderRadius: '12px',
    marginBottom: '40px',
    boxShadow: '0 4px 10px rgba(0, 0, 0, 0.05)'
  };

  const headingStyle = {
    color: '#FF0000',
    marginBottom: '12px'
  };

  return (
    <div style={{ marginTop: '40px' }}>
      <h3 style={{ color: '#FF0000', marginBottom: '30px' }}>📊 Channel Analytics</h3>

      {/* Views Per Video */}
      <div style={cardStyle}>
        <h4 style={headingStyle}>Views Per Video</h4>
        <ResponsiveContainer width="100%" height={250}>
          <BarChart data={videoStats}>
            <CartesianGrid stroke="#eee" />
            <XAxis dataKey="title" stroke="#000" />
            <YAxis stroke="#000" />
            <Tooltip />
            <Bar dataKey="views" fill="#FF0000" />
          </BarChart>
        </ResponsiveContainer>
      </div>

      {/* Likes Trend */}
      <div style={cardStyle}>
        <h4 style={headingStyle}>Likes Trend</h4>
        <ResponsiveContainer width="100%" height={250}>
          <LineChart data={videoStats}>
            <CartesianGrid stroke="#eee" />
            <XAxis dataKey="title" stroke="#000" />
            <YAxis stroke="#000" />
            <Tooltip />
            <Line type="monotone" dataKey="likes" stroke="#FF0000" strokeWidth={3} dot={{ r: 5, fill: '#FF0000' }} />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Like-to-View Ratio */}
      <div style={cardStyle}>
        <h4 style={headingStyle}>Likes-to-Views Ratio (%)</h4>
        <ResponsiveContainer width="100%" height={250}>
          <BarChart data={videoStats}>
            <CartesianGrid stroke="#eee" />
            <XAxis dataKey="title" stroke="#000" />
            <YAxis stroke="#000" />
            <Tooltip />
            <Bar dataKey="likeViewRatio" fill="#FF0000" />
          </BarChart>
        </ResponsiveContainer>
      </div>

      {/* Comments Per Video */}
      <div style={cardStyle}>
        <h4 style={headingStyle}>Comments Per Video</h4>
        <ResponsiveContainer width="100%" height={250}>
          <BarChart data={videoStats}>
            <CartesianGrid stroke="#eee" />
            <XAxis dataKey="title" stroke="#000" />
            <YAxis stroke="#000" />
            <Tooltip />
            <Bar dataKey="comments" fill="#FF0000" />
          </BarChart>
        </ResponsiveContainer>
      </div>

      {/* Channel Summary */}
      {channelData && (
        <div style={cardStyle}>
          <h4 style={headingStyle}>📈 Channel Summary</h4>
          <ResponsiveContainer width="100%" height={250}>
            <BarChart
              data={[
                {
                  metric: 'Subscribers',
                  value: parseInt(channelData.statistics.subscriberCount),
                },
                {
                  metric: 'Total Views',
                  value: parseInt(channelData.statistics.viewCount),
                },
                {
                  metric: 'Total Videos',
                  value: parseInt(channelData.statistics.videoCount),
                }
              ]}
            >
              <CartesianGrid stroke="#eee" />
              <XAxis dataKey="metric" stroke="#000" />
              <YAxis stroke="#000" />
              <Tooltip />
              <Bar dataKey="value" fill="#FF0000" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}
    </div>
  );
};

export default ChartsDashboard;
