import React, { useState } from 'react';
import LikesTrendChart from './LikesTrendChart';
import VideoViewsChart from './VideoViewsChart';
import ChartsDashboard from './ChartsDashboard';
import './App.css';

function App() {
  const [channelName, setChannelName] = useState('');
  const [channelData, setChannelData] = useState(null);
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const fetchData = async () => {
    setLoading(true);
    setError('');
    try {
      const res = await fetch(`https://youtube-backend-s9gi.onrender.com/channel?name=${channelName}`);
      const result = await res.json();

      if (result.channel && result.videos.length > 0) {
        setChannelData(result.channel);
        setVideos(result.videos);
      } else {
        setError('No data found for this channel.');
        setChannelData(null);
        setVideos([]);
      }
    } catch (err) {
      setError('Failed to fetch data. Please try again.');
      setChannelData(null);
      setVideos([]);
    }
    setLoading(false);
  };

  return (
    <div className="wrapper">
      <div className="App">
        <h1>YouTube Channel Analytics</h1>

        <div className="input-group">
          <input
            type="text"
            value={channelName}
            onChange={(e) => setChannelName(e.target.value)}
            placeholder="Enter YouTube Channel Name"
          />
          <button onClick={fetchData}>Get Analytics</button>
        </div>

        {loading && <p style={{ marginTop: '20px' }}>⏳ Loading data...</p>}
        {error && <p style={{ marginTop: '20px', color: 'red' }}>{error}</p>}

        {channelData && (
          <div className="channel-info">
            <h2>{channelData.snippet.title}</h2>
            <img src={channelData.snippet.thumbnails.high.url} alt="channel logo" width={200} />
            <p><strong>Subscribers:</strong> {channelData.statistics.subscriberCount}</p>
            <p><strong>Total Views:</strong> {channelData.statistics.viewCount}</p>
            <p><strong>Videos:</strong> {channelData.statistics.videoCount}</p>
            <p><strong>Channel Description:</strong> {channelData.snippet.description}</p>
            <p><strong>Created On:</strong> {new Date(channelData.snippet.publishedAt).toDateString()}</p>
            <p><strong>Country:</strong> {channelData.snippet.country || 'N/A'}</p>
          </div>
        )}

        {videos.length > 0 && (
          <>
            <ChartsDashboard videos={videos} channelData={channelData} />
            <LikesTrendChart videos={videos} />
            <VideoViewsChart videos={videos} />
          </>
        )}

        {videos.length > 0 && (
          <div style={{ marginTop: '40px' }}>
            <h3>📹 Latest Videos</h3>
            {videos.map(video => (
              <div
                key={video.id}
                style={{
                  marginBottom: '20px',
                  paddingBottom: '10px',
                  borderBottom: '1px solid #ddd',
                }}
              >
                <img src={video.snippet.thumbnails.medium.url} alt="thumbnail" width={240} />
                <h4>{video.snippet.title}</h4>
                <p><strong>Views:</strong> {video.statistics.viewCount}</p>
                <p><strong>Likes:</strong> {video.statistics.likeCount}</p>
                <p><strong>Published:</strong> {new Date(video.snippet.publishedAt).toDateString()}</p>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
